


































































export default {
	data() {
		return {
			isLoad: '',
			loadModal: false,
			loadProgress: 0
		};
	},
	methods: {
		isLoading(e) {
			this.isLoad = e.detail.value
		},
		mloadModal() {
			this.loadModal = true
			setTimeout(() => {
				this.loadModal = false
			}, 2000)
		},
		mloadProgress() {
			this.loadProgress = this.loadProgress + 3
			if (this.loadProgress < 100) {
				setTimeout(() => {
					this.mloadProgress();
				}, 100)
			} else {
				this.loadProgress = 0
			}
		}
	}
}
